import { Question } from './question';
import { quiz, info } from './data';
import { Injectable } from '@angular/core';
import { of, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AppService {

  private quiz: Question[] = [];

  constructor() {
    quiz.forEach(e =>
      this.quiz.push(new Question(
        e.id,
        e.category,
        e.question,
        e.oa,
        e.ob,
        e.oc
      ))
    );
   }

  public getQuestionsList(): Observable<Question[]> {
    return of(this.quiz);
  }

  public getInfoList(): Observable<any> {
    return of(info);
  }

}
